<template>
  <PopUp :flow='pldcStatus'>
    <div class="box">
      <div class="container">
        <i class="iconfont close" @click='closePldc'>&#xe648;</i>
        <div class="title clearfix">
          <i class="iconfont title-img">&#xe61e;</i>
          <span class="title-span">{{$t("history.pldc")}}</span>
        </div>
        <div class="content">
          <div class="dcsz-form">
            <div class="dcsz-top">
              <div class="dcsz-top-left">
                <span>{{$t("history.sblx")}}：</span>
              </div>
              <div class="dcsz-top-right">
                <el-select class='select' v-model='condition.machineTypeId' filterable :placeholder="$t('message.select')" @change='isTwFn'>
                 <el-option
                   v-for="item in machineTypeList"
                   :key="item.id"
                   :label="item.name"
                   :value="item.id">
                 </el-option>
               </el-select>
              </div>
            </div>
            <div class="dcsz-top">
              <div class="dcsz-top-left">
                <span>{{$t("history.dcrq")}}：</span>
              </div>
              <div class="dcsz-top-right">
                <el-date-picker
                  v-model="condition.date"
                  type="date"
                  class='datepicker'
                  value-format="yyyy-MM-dd"
                  @change='dateChangeExtendFn'
                  :placeholder="$t('message.select')">
                </el-date-picker>
              </div>
            </div>
            <div class="dcsz-bottom-pr">
              <div class="dcsz-bottom">
                <div class="dcsz-bottom-left">
                  <span>{{$t("history.xzsjd")}}：</span>
                </div>
                <div class="dcsz-bottom-right">
                    <a href="javascript:void(0)" :class="jcType==0?'a-active':''" @click="activeTp(0)">{{$t("history.jcd4")}}</a>
                    <a href="javascript:void(0)" :class="jcType==1?'a-active':''" @click="activeTp(1)">{{$t("history.jcd6")}}</a>
                </div>
              </div>
              <div class="dcsz-bottom">
                <div class="dcsz-bottom-left">
                  <span></span>
                </div>
                <div class="dcsz-bottom-right select-time">
                  <div v-for="(item,index) in exTime.time" :key="index" class="select-time-warp" v-if='jcType==0'>
                    <el-time-picker style="width: 100px"
                      v-model="exTime.time[index]"
                      format='HH:mm'
                      value-format="HH:mm"
                      @change='timeChange(index,$event)'
                      :picker-options="{

                      }"
                      :placeholder="$t('message.select')">
                    </el-time-picker>
                    <i class='el-icon-error close-circle' @click="deleArr(index)"></i>
                  </div>
                  <div v-for="(item,index) in exTime.time" :key="index" class="select-time-warp" v-if='jcType==1'>
                    <el-time-picker style="width: 100px"
                      v-model="exTime.time[index]"
                      format='HH:mm'
                      value-format="HH:mm"
                      @change='timeChange(index,$event)'
                      :picker-options="{

                      }"
                      :placeholder="$t('message.select')">
                    </el-time-picker>
                    <i class='el-icon-error close-circle' @click="deleArr(index)"></i>
                  </div>
                  <i class="el-icon-plus add-circle" @click="addArr"></i>
                </div>
              </div>
            </div>

          </div>
        </div>
        <div class="ipt-group clearfix">
          <a href="javascript:void(0)" class="on" @click='extend'>{{$t("button.export")}}</a>
          <a href="javascript:void(0)" class="off" @click="closePldc">{{$t("button.close")}}</a>
        </div>
      </div>
    </div>
  </PopUp>
</template>

<script>
import popUp from '../../components/popUp'
import { getMachineTypeList,exportData } from '@/api/history'

var that= {};
  export default {
    name: 'pldc',
      data(){
          return {
            machineTypeList:[],
            condition:{
              machineTypeId:'',
              date:''
            },
            typeCode:'',
            jcType:0,
            exTime:{
             date:"",
             time:["06:00",'10:00','14:00','18:00']
           },
          }
      },
      props:{
        pldcStatus:{
          type:Boolean
        },
        departmentId: {
          type: Number
        },
      },
      beforeCreate(){
          that = this;
      },
      mounted:function(){
        that.getMachineTypeListFn();
      },
      methods:{
        closePldc(pldcStatus){
          that.$emit('getpldcStatus',!that.pldcStatus)
        },
        /*获取设备类型*/
        getMachineTypeListFn(){
          getMachineTypeList().then((res)=>{
            if(res.code==200 && res.success){
              that.machineTypeList = res.data;
              that.condition.machineTypeId = that.machineTypeList[0].id;
              that.typeCode = that.machineTypeList[0].typeCode;
            }
          })
        },
        isTwFn(departmentId){
          that.condition.machineTypeId = departmentId;
          console.log(departmentId)
          that.machineTypeList.map(function(item){
            if(item.id == that.condition.machineTypeId){
              that.typeCode = item.typeCode;
            }
          })
        },
        /*选择导出日期*/
        dateChangeExtendFn(d){
          this.exTime.date = d;
          console.log(this.exTime)
        },
        deleArr(e){
          this.exTime.time.splice(e,1)
           console.log(e)
        },
        addArr(){
          if(this.exTime.time.length<10){
            this.exTime.time.push("")
          }else{
            that.error(that.$t("history.err.over10"))
          }
        },
        /*选择导出时间*/
        timeChange(d,t){
          console.log(this.exTime)
           this.exTime.time[d] = t;
        },
        activeTp(type){
          that.jcType = type;
          if(type==0){
            that.exTime.time=["06:00",'10:00','14:00','18:00'];
          }else if(type==1){
            that.exTime.time=['02:00',"06:00",'10:00','14:00','18:00','22:00']
          }else{}
        },
        /*批量导出*/
        extend(){
          console.log(that.exTime)
          if(that.exTime.date==''){
            that.error(that.$t("history.err.xzdcrq"))
          }else if(!this.exTime.time.some((item)=>{
            if(item){
              return true
            }
          })||this.exTime.time.length ==0){
            that.error(that.$t("history.err.xzdcsj"))
          }else{
              var timeList = "";
              for(var i=0;i<that.exTime.time.length;i++){
                  if(that.exTime.time[i] !=""){
                      timeList = timeList+","+that.exTime.time[i]+":00"
                  }
              }
              console.log(timeList.replace(/^,+/,"").replace(/,+$/,""))
              var temType;
              var language;
              if(that.$store.state.monitor.selectTem){
                temType = 0;
              }else{
                temType = 1;
              }
              if(localStorage.getItem('locale')=='en'){
                language = 1;
              }else{
                language = 0;
              }
              var data={
                timeList:timeList.replace(/^,+/,"").replace(/,+$/,""),
                date:that.exTime.date,
                departmentId:that.departmentId,
                type:that.typeCode,
                token:that.$store.state.login.token,
                temType:temType,
                language:language
              }
              exportData(data).then((res)=>{
                console.log(res)
                if(res.data.rCode=='success'){
                  that.success(that.$t("message.OperSuccess"));
                  that.$emit('getpldcStatus',!that.pldcStatus)
                }
              })

          }

        },
      },
      components:{
          'PopUp': popUp,
      }
  }
</script>
 <style scoped lang="scss">
 .content{
    margin:0 42px;
    .dcsz-form{
      padding:40px 70px 53px 70px;
      .dcsz-top{
        display:flex;
        flex-direction: row;
        margin-bottom:32px;
        .dcsz-top-left{
          width:150px;
          display:flex;
          align-items: center;
          justify-content: flex-end;
          span{
            font-size:16px;
             font-family:PingFangSC-Regular,PingFang SC;
             font-weight:400;
             color:rgba(102,102,102,1);
             line-height:22px;
          }
        }
        .dcsz-top-right{
          display:flex;
          flex-direction: column;
          span{
            font-size:16px;
            font-family:PingFangSC-Medium,PingFang SC;
            font-weight:600;
            color:rgba(35,35,35,1);
            line-height:22px;
          }
        }
      }
      .dcsz-bottom-pr{
        // width:650px;
        .dcsz-bottom{
          display:flex;
          flex-direction: row;
          .dcsz-bottom-left{
            width:150px;
            display:flex;
            justify-content: flex-end;
            align-items: center;
            span{
              font-size:16px;
             font-family:PingFangSC-Regular,PingFang SC;
             font-weight:400;
             color:rgba(102,102,102,1);
             line-height:22px;
             display:block;
            }
          }
          .dcsz-bottom-right{
            display:flex;
            flex-direction: row;
            .add-circle{
               color: #0091FF ;
               margin-top: 30px;
               float: left;
               cursor: pointer;
             }
            a{
              display:block;
              padding:0 10px;
              height:34px;
              line-height:34px;
              border-radius:18px;
              margin-right:21px;
              background:#fff;
              border:1px solid rgba(24,144,255,1);
              color:rgba(24,144,255,1);
            }
            a:nth-child(3){
              margin-right:0;
            }
            .a-active{
              background:rgba(24,144,255,1);
              border:1px solid rgba(24,144,255,1);
              color:#fff;
            }
            .select-time-warp{
              position:relative;
              margin-right:20px;
              margin-top:20px;
              .close-circle{
                position:absolute;
                font-size:20px;
                top:-8px;
                right:-8px;
                color: #E02020;
              }
            }
          }
          .select-time{
            width:550px;
            flex-wrap: wrap;
          }
        }
      }

    }
 }
.dcsz-top-right /deep/ .el-input__inner{
  height:36px;
  width:170px;
}
.select-time-warp /deep/ .el-input__inner{
  height:36px;
}
 </style>



