<template>
  <PopUp :flow='xyStatus'>
    <div class="box">
      <div class="container">
        <i class="iconfont close" @click='closeXy'>&#xe648;</i>
        <div class="title clearfix">
          <i class="iconfont title-img">&#xe649;</i>
          <span class="title-span">{{$t("history.title")}}</span>
        </div>
        <div class="content">
          <div class='msg'>
            <span>{{currentRecord.roomAndBed}} &emsp;{{currentRecord.patientName}} &emsp;{{currentRecord.patientCaseNum}}</span>
          </div>
          <div class="select-pd">
            <span>{{$t("history.xzcjxm")}}</span>
            <a href="javascript:void(0)" :class="pdType==0?'lsjl-active':''" @click='selectPd(0)'>{{$t("history.xy")}}/PI</a>
            <a href="javascript:void(0)" :class="pdType==1?'lsjl-active':''" @click='selectPd(1)'>{{$t("history.xl")}}</a>
          </div>
          <div class="zx-pic" id='myChart' style="width:798px;height:355px;"></div>
          <div class="" v-if='pdType==0'>
            <div class="history-tj-xy">
              <div class="history-tj-xy-item">
                <em class='em1'></em>
                <span>{{$t("history.gzzs")}}</span>
              </div>
              <div class="history-tj-xy-item">
                <em class='em2'></em>
                <span>{{$t("history.xy")}}</span>
              </div>
            </div>
          </div>
        </div>
        <div class="ipt-group clearfix">
          <a href="javascript:void(0)" class="on" @click='closeXy'>{{$t('button.confirm')}}</a>
          <a href="javascript:void(0)" class="off" @click='closeXy'>{{$t('button.close')}}</a>
        </div>
      </div>
    </div>
  </PopUp>
</template>

<script>
import popUp from '../../components/popUp'
import { getCurrentHistory } from '@/api/history'
var that= {};
  export default {
    name: 'tjzc',
      data(){
          return {
            heartRateList:[],
            bloodList:[],
            timesList:[],
            piList:[],
            pdType:0,
          }
      },
      props:{
        xyStatus:{
          type:Boolean
        },
        departmentId: {
          type: Number
        },
        currentRecord:{
          type:Object
        },
      },

      beforeCreate(){
          that = this;
      },
      mounted:function(){
        that.getCurrentHistoryFn();
      },
      methods:{
        getCurrentHistoryFn(){
          var arr = [];
          var data={
            type:2,
            recordId:that.currentRecord.recordId
          }
          getCurrentHistory(data).then((res)=>{
            if(res.code==200 && res.success){
              if(res.data==''){
                that.twHistoryData = [];
              }else{
                that.twHistoryData = res.data.history;
              }
                that.twHistoryData.map(function(item){
                  item.map(function(it){
                    var i = that.timestampToTime(it.timestamp);
                    it.timestamp = i;
                    that.timesList.push(it.timestamp.slice(5,16));
                    that.bloodList.push(it.bloodOxygen);
                    that.heartRateList.push(it.heartRate);
                    that.piList.push(it.pi)
                  })
                })
                console.log(that.twHistoryData)
                that.drawLineXyPI();
            }
          })
        },
        selectPd(type){
          that.pdType = type;
          if(type==0){
            that.drawLineXyPI();
          }else if(type==1){
            that.drawLineXL();
          }
        },
        closeXy(xyStatus){
          that.$emit('getxyStatus',!that.xyStatus)
        },
        /*绘制图表*/
        drawLineXL(){
          let myChart = that.$echarts.init(document.getElementById('myChart'));
          myChart.clear();
          myChart.setOption({
             tooltip: {
               trigger: 'axis',
             },
             grid:{//图标留白区域
               top:'14px',
               left:'60px',
               right:'30px',
               bottom:'33px'
             },
             xAxis: {
                type: 'category',
                boundaryGap: false,
                axisLine:{symbol:['none', 'arrow']},
                data: that.timesList,
                axisLabel: {
                   rotate:0,
                },
              },
              yAxis: {
                  min:10,
                   max: 130,
                   interval: 10,
                  axisLabel: {
                  formatter: '{value}'
                },
                  axisLine:{symbol:['none', 'arrow']},
              },
              dataZoom: [{
                 start:0,
                 end:50
             }, {
                 type: 'inside',
                 start:0,
                 end:50
             }, {
                 start:0,
                 end:50
             },],
              series: [{
                data: that.heartRateList,
                symbol: 'circle',
                sampling: 'average',
                formatter: '{value}',
                type: 'line',
              lineStyle:{
                color:"#7C6AF2",
                width:3
              },
              itemStyle:{
                normal:{
                  borderWidth:0,
                  color:'#7C6AF2',
                  borderColor:'#7C6AF2',
                }
              },
            }]
         });
        },

        drawLineXyPI(){
          let myChart = this.$echarts.init(document.getElementById('myChart'));
          myChart.clear();
          myChart.setOption({
              tooltip: {
               trigger: 'axis',
              },
              grid:{//图标留白区域
                top:'14px',
                left:'60px',
                right:'30px',
                bottom:'33px'
              },
              xAxis: [
                {
                  type: 'category',
                  boundaryGap: false,
                  axisLine:{symbol:['none', 'arrow']},
                  data: that.timesList,
                  axisLabel: {
                     rotate:0,
                  },
                },
              ],
              yAxis: {
                  min: 0,
                   max: 130,
                   interval: 10,
                  axisLabel: {
                  formatter: '{value}'
                },
                  axisLine:{symbol:['none', 'arrow']},
              },
              dataZoom: [{
                 start:0,
                 end:50
              }, {
                 type: 'inside',
                 start:0,
                 end:50
              }, {
                 start:0,
                 end:50
              },],
              series: [
                {
                  data: that.bloodList,
                  name:that.$t("history.xy"),
                  symbol: 'circle',
                  sampling: 'average',
                  formatter: '{value}',
                  type: 'line',
                  lineStyle:{
                    color:"#ff39fa",
                    width:3
                  },
                  itemStyle:{
                    normal:{
                      borderWidth:0,
                      color:'#ff39fa',
                      borderColor:'#ff39fa',
                    }
                  },
                },
                {
                  data: that.piList,
                  name:that.$t("history.gzzs"),
                  symbol: 'circle',
                  sampling: 'average',
                  formatter: '{value}',
                  type: 'line',
                  lineStyle:{
                    color:"#7C6AF2",
                    width:3
                  },
                  itemStyle:{
                    normal:{
                      borderWidth:0,
                      color:'rgba(94,119,255,1)',
                      borderColor:'rgba(94,119,255,1)',
                    }
                  },
                }
              ]
           });
        },
      },
      components:{
          'PopUp': popUp,
      }
  }
</script>
<style scoped lang="scss">
.content{
  margin:0 42px;
  flex-direction: column;
  .msg{
    display:flex;
    span{
      font-size:18px;
      font-family:PingFangSC-Medium,PingFang SC;
      font-weight:500;
      color:rgba(153,153,153,1);
      text-align: left;
      padding-left:22px;
      margin:18px 0 13px;
    }

  }
  .select-pd{
    padding-left:22px;
    display:flex;
    margin-bottom:14px;
    span{
      font-size:18px;
      font-family:PingFangSC-Medium,PingFang SC;
      font-weight:500;
      color:rgba(51,51,51,1);
      margin-right:10px;
      line-height:30px;
    }
    a{
      display:inline-block;
      padding:6px 24px;
      border-radius:17px;
      border:1px solid rgba(24,144,255,1);
      color:#1890FF;
      margin-right:22px;
    }
    .lsjl-active{
      background:rgba(24,144,255,1);
      color:#fff;
    }
  }
  .history-tj-xy{
    padding:16px 0 30px 0;
    width:400px;
    display:flex;
    justify-content: space-between;
    margin:0 auto;
    .history-tj-xy-item{
      font-size:18px;
      font-family:PingFangSC-Medium,PingFang SC;
      font-weight:500;
      color:rgba(38,38,38,1);
      display:flex;
      em{
        font-size:16px;
        font-family:PingFangSC-Medium,PingFang SC;
        font-weight:500;
        color:rgba(40,45,50,1);
        display:block;
        width:25px;
        height:3px;
        margin-top:10px;
        margin-right:5px;
      }
      .em1{
        background:#7C6AF2;
      }
      .em2{
        background:#ff39fa;
      }
    }
  }
  .history-tj-xlhx{
    width:500px;
    margin:3px auto 0 auto;
    .xlhx-row{
      display:flex;
      justify-content: space-between;
      margin-bottom:16px;
      .ys{
        display:flex;
        justify-content: space-between;
        i{
          width:25px;
          height:1px;
          display:block;
          margin-top: 8px;
        }
        .xl{
          background:#7C6AF2;
        }
        .hx{
          background:#5C89FF;
        }
        span{
          display:block;
          width:60px;
          font-size:16px;
          font-family:PingFangSC-Medium,PingFang SC;
          font-weight:500;
          color:rgba(40,45,50,1);
        }
      }
    }
  }
}
</style>



