<template>
  <div class="history-all">
    <img src="../assets/img/bg.png" alt="" class='bg'>
    <Topbar>
      <i class='iconfont'>&#xe61e;</i>
      <span>{{$t("history.title")}}</span>
    </Topbar>
    <div class="history-main">
      <div class="history-keyword">
        <div class="history-row">
          <div class="row-left">
            <span class="choose-title">{{$t("history.qyrq")}}:</span>
            <el-date-picker
              v-model="condition.date.dateChangeStart"
              type="date"
              @change='dateChangeStartFn'
              :picker-options="options5"
              value-format="yyyy-MM-dd"
              :placeholder="$t('history.xzrq')">
            </el-date-picker>
            <span class="choose-title choose-title1">{{$t("history.to")}}</span>
            <el-date-picker
              v-model="condition.date.dateChangeEnd"
              type="date"
              @change='dateChangeEndFn'
              :picker-options="options4"
              value-format="yyyy-MM-dd"
              :placeholder="$t('history.xzrq')">
            </el-date-picker>
          </div>
          <div class="row-right">
            <div class="keyword">
              <span class="choose-title">{{$t("history.brxm")}}:</span>
              <el-input v-model="condition.patientName" class='history-select' :placeholder="$t('history.err.srxm')"></el-input>
            </div>
            <div class="buttons">
              <a href="javascript:void(0)" @click='getHistoryListFns'>{{$t("history.search")}}</a>
              <a href="javascript:void(0)" @click='pldcStatus = !pldcStatus'>{{$t("history.dc")}}</a>
            </div>
          </div>
        </div>
        <div class="history-row">
          <div class="row-left">
            <span class="choose-title">{{$t("history.ksmc")}}:</span>
            <el-select class='history-select' v-model='condition.departmentId' filterable :placeholder="$t('message.select')" @change='selectDepartment'>
             <el-option
               v-for="item in departmentList"
               :key="item.departmentId"
               :label="item.name"
               :value="item.departmentId">
             </el-option>
           </el-select>
          </div>
          <div class="row-right">
            <div class="keyword">
              <span class="choose-title">{{$t("history.sbmc")}}:</span>
              <div class="choose-item">
                <a href="javascript:void(0)" v-for="item in machineTypeList" :key="item.id" @click='getType(item.typeCode)' :class="condition.type==item.typeCode?'choose-active':''">{{item.name}}</a>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="main">
        <el-table :data="historyList" style="width: 100%" :empty-text="$t('message.noData')">
          <el-table-column prop="recordId" :label="$t('history.bh')" width="100"></el-table-column>
          <el-table-column prop="patientName" :label="$t('history.xm')"></el-table-column>
          <el-table-column prop="patientCaseNum" :label="$t('history.zyh')"></el-table-column>
          <el-table-column prop="departmentName" :label="$t('history.ks')"></el-table-column>
          <el-table-column prop="roomAndBed" :label="$t('history.bfks')"></el-table-column>
          <el-table-column prop="machineName" :label="$t('history.sbmc')"></el-table-column>
          <el-table-column prop="startTime" :label="$t('history.qysj')"></el-table-column>
          <el-table-column prop="endTime" :label="$t('history.tzsj')"></el-table-column>
          <el-table-column prop="usageState" :label="$t('history.zt')">
            <template slot-scope="scope">
              <span v-if="historyList[scope.$index].recordStatus=='0'" style="color:#1890ff">{{$t("history.syz")}}</span>
              <span v-if="historyList[scope.$index].recordStatus=='1'">{{$t("history.js")}}</span>
            </template>
          </el-table-column>
          <el-table-column prop="action" :label="$t('history.cz')">
            <template slot-scope="scope">
              <div class="options" v-if='condition.type==0'>
                <i class='iconfont' @click='twDraw(scope.row,0)' style='cursor:pointer'>&#xe649;</i>
                <em></em>
                <i class='iconfont' @click='exportSet(scope.row,0)' style='cursor:pointer'>&#xe63d;</i>
              </div>
              <div class="options" v-if='condition.type==1'>
                <i class='iconfont' @click='twDraw(scope.row,1)' style='cursor:pointer'>&#xe649;</i>
                <em></em>
                <i class='iconfont' @click='exportSet(scope.row,1)' style='cursor:pointer'>&#xe63d;</i>
              </div>
              <div class="options" v-if='condition.type==2'>
                <i class='iconfont' @click='twDraw(scope.row,2)' style='cursor:pointer'>&#xe649;</i>
                <em></em>
                <i class='iconfont' @click='exportSet(scope.row,2)' style='cursor:pointer'>&#xe63d;</i>
              </div>
              <div class="options" v-if='condition.type==3'>
                <i class='iconfont' @click='twDraw(scope.row,3)' style='cursor:pointer'>&#xe649;</i>
                <em></em>
                <i class='iconfont' @click='exportSet(scope.row,3)' style='cursor:pointer'>&#xe63d;</i>
              </div>
            </template>
          </el-table-column>
        </el-table>
      </div>
      <el-pagination
        background
        :page-size='pageSize'
        :current-page='currentPage'
        @current-change="handleCurrentChange"
        layout="prev, pager, next"
        :total="historyData.records">
      </el-pagination>
    </div>
    <dcsz :dcszStatus='dcszStatus' v-if='dcszStatus' @getdcszStatus='getdcszStatus' :currentRecord='currentRecord' :currentHistorytype='currentHistorytype'></dcsz>
    <pldc :pldcStatus='pldcStatus' v-if='pldcStatus' @getpldcStatus='getpldcStatus' :departmentId='condition.departmentId'></pldc>
    <!-- 三个历史记录 -->
    <tw :twStatus='twStatus' v-if='twStatus' @gettwStatus='gettwStatus' :departmentId='condition.departmentId' :currentRecord='currentRecord'></tw>
    <xd :xdStatus='xdStatus' v-if='xdStatus' @getxdStatus='getxdStatus' :departmentId='condition.departmentId' :currentRecord='currentRecord'></xd>
    <xy :xyStatus='xyStatus' v-if='xyStatus' @getxyStatus='getxyStatus' :departmentId='condition.departmentId' :currentRecord='currentRecord'></xy>
    <lc :lcStatus='lcStatus' v-if='lcStatus' @getlcStatus='getlcStatus' :departmentId='condition.departmentId' :currentRecord='currentRecord'></lc>
  </div>

</template>
<script>
import { mapState,mapActions} from 'vuex'
import tw from "../components/history/tw.vue"
import xd from "../components/history/xd.vue"
import xy from "../components/history/xy.vue"
import lc from "../components/history/lc.vue"
import dcsz from "../components/history/dcsz.vue"
import pldc from "../components/history/pldc.vue"
import Topbar from '../components/topbar'
import { getMachineTypeList,getHistoryList,getCurrentHistory  } from '@/api/history'

var that = {};
export default {
  name: 'history',
  data () {
    return {
      twStatus:false,
      xdStatus:false,
      xyStatus:false,
      lcStatus:false,
      dcszStatus:false,
      pldcStatus:false,
      departmentList:[],
      currentRecord:{},
      historyData:[],

      options4: {
         disabledDate (date) {
             return date && date.valueOf() < new Date(that.condition.date.dateChangeStart);
         }
       },
       options5: {
           disabledDate (date) {
               return date && date.valueOf() > new Date(that.condition.date.dateChangeEnd);
           }
       },
       condition:{
         date:{
           dateChangeStart:'',
           dateChangeEnd:''
         },
         departmentId:'',
         patientName:'',
         type:0
       },
       pageSize:that.global.pageSize,
       currentPage:that.global.currentPage,
       historyList:[],
       twHistoryData:[],/*某条体温设备的历史数据*/
       language:that.$store.state.login.language,

      machineTypeList:[],
      options5: {
         disabledDate (date) {
             return date && date.valueOf() > new Date(that.condition.date.dateChangeEnd);
         }
     },
     options4: {
         disabledDate (date) {
             return date && date.valueOf() < new Date(that.condition.date.dateChangeStart);
         }
     },
      dateChangeStart:'',
      dateChangeEnd:'',
      pageSize:that.global.pageSize,
      currentPage:that.global.currentPage,
      historyData:[]
    }
  },
  beforeCreate(){
      that = this;
  },
  mounted:function(){
    that.getNowTime();
    console.log(that.$store.state.login.userInfo)
    console.log(that.$store.state.monitor)
    if(that.$store.state.login.userInfo.role==2){
      that.$store.state.monitor.departmentList.map(function(item){
        if(item.departmentId==that.$store.state.login.userInfo.departmentId){
          that.departmentList.push(item);
          that.condition.departmentId = that.departmentList[0].departmentId;
        }
      })
    }else{
      that.departmentList = that.$store.state.monitor.departmentList;
      that.condition.departmentId = that.departmentList[0].departmentId;
    }
    that.getHistoryListFn();
    that.getMachineTypeListFn();
  },
  methods:{
    handleCurrentChange(value){
      console.log(value)
      that.currentPage = value;
      that.getHistoryListFn();
    },
    gettwStatus(twStatus){
      that.twStatus = twStatus;
    },
    getxdStatus(xdStatus){
      that.xdStatus = xdStatus;
    },
    getpldcStatus(pldcStatus){
      that.pldcStatus = pldcStatus;
    },
    getxyStatus(xyStatus){
      that.xyStatus = xyStatus;
    },
    getlcStatus(lcStatus){
      that.lcStatus = lcStatus;
    },
    getdcszStatus(dcszStatus){
      that.dcszStatus = dcszStatus;
    },
    getMachineTypeListFn(){
      getMachineTypeList().then((res)=>{
        if(res.code==200 && res.success){
          that.machineTypeList = res.data;
        }
      })
    },
    twDraw(row,type){
      console.log(row)
      that.currentHistorytype = type;
      that.currentRecord = row;
      if(that.currentHistorytype==0){
        that.twStatus = !that.twStatus;
      }else if(that.currentHistorytype==1){
        that.xdStatus = !that.xdStatus;
      }else if(that.currentHistorytype==2){
        that.xyStatus = !that.xyStatus;
      }else if(that.currentHistorytype==3){
        that.lcStatus = !that.lcStatus;
      }
    },
    /*导出设置*/
    exportSet(row,type){
      console.log(row,type)
      that.currentHistorytype = type;
      that.currentRecord = row;
      that.dcszStatus = !that.dcszStatus;
    },
    selectDepartment(departmentId){
      that.condition.departmentId = departmentId;
      that.currentPage = that.global.currentPage;
      that.getHistoryListFn();
    },
    getType(type){
      that.condition.type = type;
      that.currentPage = that.global.currentPage;
      that.getHistoryListFn();
    },
    /*获取今天的日期*/
    getNowTime(){
      var date = new Date();
       var year = date.getFullYear();
       var month = date.getMonth() + 1;
       var strDate = date.getDate();
       if(month<10){
         month = '0'+month;
       }
       if(strDate<10){
         strDate = '0'+strDate;
       }
       that.condition.date={
         dateChangeStart:year+'-'+month+'-'+strDate,
         dateChangeEnd : year+'-'+month+'-'+strDate
       }
    },
    getHistoryListFns(){
      that.currentPage = that.global.currentPage;
      that.getHistoryListFn();
    },
    /*获取历史记录*/
    getHistoryListFn(){
      var data={
        pageSize:that.global.pageSize,
        page:that.currentPage,
        type:that.condition.type
      }
      console.log(that.condition.date.dateChangeEnd)
      if(that.condition.date.dateChangeStart!='' && that.condition.date.dateChangeStart!=null){
        data['startDate'] = that.condition.date.dateChangeStart;
      }
      if(that.condition.date.dateChangeEnd!='' && that.condition.date.dateChangeEnd!=null){
        data['endDate'] = that.condition.date.dateChangeEnd;
      }
      if(that.condition.patientName!=''){
        data['patientName'] = that.condition.patientName;
      }
      if(that.condition.departmentId!=''){
        data['departmentId'] = that.condition.departmentId;
      }
      console.log(data)
      getHistoryList(data).then((res)=>{
        if(res.code==200 && res.success){
          that.historyData = res.data;
          that.historyList = res.data.rows;
        }
      })
    },
    /*选择检索开始日期*/
    dateChangeStartFn(d){
      that.condition.date.dateChangeStart = d;
    },
    /*选择检索结束日期*/
    dateChangeEndFn(d){
      that.condition.date.dateChangeEnd = d;
    },
  },
  components:{
    dcsz,
    pldc,
    tw,
    xd,
    xy,
    lc,
    // lsjl,
    Topbar
  },
}
</script>
<style lang="scss">
html,body{
  width:100%;
  height:100%;
  .history-all{
    width:100%;
    background-size:cover;
    height:100%;
    position:relative;
    padding-top:40px;
    .history-main{
      position:relative;
      margin:0 auto;
      width:83%;
      .history-keyword{
        display:flex;
        flex-direction: column;
        padding-top:47px;
        .history-row{
          display:flex;
          flex-direction: row;
          margin-bottom:23px;
          .row-left{
            width:43%;
            display:flex;
            flex-direction: row;
            .choose-title{
              font-size:18px;
              font-family:PingFangSC-Medium,PingFang SC;
              font-weight:600;
              color:rgba(0,0,0,1);
              line-height:36px;
              margin-right:7px;
              height:36px;
            }
            .choose-title1{
              margin:0 17px;
            }

          }
          .row-right{
            width:57%;
            display:flex;
            flex-direction: row;
            justify-content: space-between;
            .keyword{
              .choose-title{
                float:left;
                font-size:18px;
                font-family:PingFangSC-Medium,PingFang SC;
                font-weight:600;
                color:rgba(0,0,0,1);
                line-height:36px;
                margin-right:7px;
                height:36px;
              }
              .choose-item{
                display:flex;
                a{
                  display:block;
                  padding:0 10px;
                  height:34px;
                  margin-right:10px;
                  border-radius:17px;
                  border:1px solid rgba(24,144,255,1);
                  color:rgba(24,144,255,1);
                  font-size:13px;
                  display:flex;
                  align-items: center;
                  justify-content: center;
                  text-align:center;
                  word-wrap: break-word;
                  word-break: keep-all;
                }
                .choose-active{
                  background:rgba(24,144,255,1);
                  color:#fff;
                }
              }
              .history-select{
                float:left;
                width:auto;
              }
            }
            .buttons{
              display:flex;
              flex-direction: row;
              a{
                display:block;
                width:109px;
                height:35px;
                background:rgba(24,144,255,1);
                border-radius:2px;
                font-size:14px;
                font-family:PingFangSC-Regular,PingFang SC;
                font-weight:400;
                color:rgba(255,255,255,1);
                line-height:35px;
                text-align:center;
              }
              a:first-of-type{
                margin-right:12px;
              }
            }
          }
        }
      }
      .main th,.main td{
        text-align:center;
        .options{
          display:flex;
          flex-direction: row;
          justify-content: center;
          em{
            font-style: normal;
            width:1px;
            height:20px;
            display:block;
            margin:0 15px;
            background:#D8D8D8;
          }
          .iconfont{
            color:#000;
            font-size:22px;
          }
        }
      }
      .main th{
        font-size:16px;
        font-family:PingFangSC-Regular,PingFang SC;
        font-weight:600;
        color:rgba(0,0,0,1);
        .cell{
          word-wrap: break-word;
          word-break: keep-all;
        }
      }
      .main td{
        font-size:14px;
        font-family:PingFangSC-Regular,PingFang SC;
        font-weight:400;
        color:#A3A3A3;
      }
    }
  }
}

.history-select /deep/ .el-input__inner{
  height:36px;
  width:200px;
}
.main /deep/ .el-pagination{
  display:flex;
  justify-content: flex-end;
  padding:21px 0;
}
.main .el-pagination{
  display:flex;
  justify-content: flex-end;
  padding:21px 0;
  float:right;
}
.el-date-editor{
  width:150px!important;
}
.el-date-editor .el-input{
  width:150px;
}
.el-date-editor .el-input__inner{
  width:150px;
}
</style>



